body {
  background: #1f1e1a;
  font-family: sans-serif;
  color:#fffede;
}
h1 {
  font-weight: normal;
}
.btn {
  display: inline-block;
  cursor: pointer;
  background: #1f1e1a;
  color: #869093;
  box-shadow: 0 0 1px rgba(0, 0, 0, .2);
  padding: .5em .8em;
  transition: box-shadow .05s ease-in-out;
  -webkit-transition: box-shadow .05s ease-in-out;
}
.btn:hover {
  box-shadow: 0 0 2px rgba(0, 0, 0, .2);
}
.btn:active, .active, .active:hover {
  box-shadow: 0 0 1px rgba(0, 0, 0, .2),
              inset 0 0 4px rgba(0, 0, 0, .1);
}
.add {
  float: right;
}
#container {
  max-width: 42em;
  margin: 0 auto 2em auto;
}
.list {
  position: relative;
}
.row {
  overflow: hidden;
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  left: 0px;
  margin: .5em 0;
  padding: 1em;
  background: #1f1e1a;
  box-shadow: 0 0 1px rgba(0, 0, 0, .2);
  transition: transform .5s ease-in-out, opacity .5s ease-out, left .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out, opacity .5s ease-out, left .5s ease-in-out;
}
.row div {
  display: inline-block;
  vertical-align: middle;
}
.row > div:nth-child(1) {
  width: 5%;
}
.row > div:nth-child(2) {
  width: 30%;
}
.row > div:nth-child(3) {
  width: 65%;
}
.rm-btn {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  color: #ba967d;
  width: 1.4em;
  height: 1.4em;
  text-align: center;
  line-height: 1.4em;
  padding: 0;
}